.profile-list {
  padding: 120px 0 50px;

  h2 {
    @include fluid-type(320px, 1200px, 25px, 32px);
    padding: 50px 0 30px;
  }

  /* Profiles layout */
  .column {
    max-width: 300px;

    .card {
      max-height: 300px;
      min-height: 300px;
    }

    .card-footer {
      padding: 0 0 15px 0;
      background: #fff;
    }

    /* Profile image styles */
    .profile-image {
      width: 100%;
      height: auto;
      max-height: 150px;
      overflow: hidden;
      border-radius: 15px;
      margin-bottom: 0;

      img {
        max-width: 100%;
        max-height: 100%;
      }

      &.premium {
        position: relative !important;

        .badge {
          position: absolute !important;
          top: 10px;
          right: 10px;
          background: #fff;
          text-align: center;
        }
      }
    }

    .name.online {
      display: flex;
      align-items: center;
      justify-content: flex-start;

      .badge {
        font-size: 12px;
      }
    }
  }

  .no-premium {
    figure {
      width: 100%;
      margin: 0;

      img {
        @include image;
      }
    }
  }

  .no-results {
    .fa-heart-broken {
      @include fluid-type(320px, 1200px, 70px, 100px);
      color: $main-blue;
    }

    .title {
      @include fluid-type(320px, 1200px, 22px, 28px);
      padding: 15px 0 0;
    }
  }
}
